html{
    scroll-behavior: smooth;
}
body{
    font-family: 'Open Sans',sans-serif;
    background: #f8f8f8;
}
h1, h2, h3, h4, h5, h6 {
    font-family: "Raleway", sans-serif;
}
a{
    text-decoration: none !important;
}
.navbar{
    padding: 0 0;
}
.navbar-brand{
    font-family: 'Poppins',sans-serif;
    font-size: 32px;
    letter-spacing: 1px;
    color: #35cd3a !important;
    font-weight: 600;
    text-transform: uppercase;
}
.navbar-nav li a{
    font-family: 'Poppins',sans-serif;
    font-weight: 600 !important;
    font-size:14px !important ;
}
.navbar .navbar-nav .nav-item .nav-link {
    background: transparent !important;
    color:  #666 !important;
}
.navbar .navbar-nav .nav-item.active >a {
    background: transparent !important;
    color: #35cd3a !important;
}
.navbar .navbar-nav .nav-item .nav-link:hover {
    background: transparent !important;
    color:  #35cd3a !important;
}

/* hero section */
#hero{
    background: url('./images/hero-bg.jpg') top center;
    background-attachment: fixed;
    width: 100%;
    height: 80vh;
    padding-top: 25vh;
    position: relative;
    color:#FFFFFF;
    font-family: 'Poppins',sans-serif;
}
#hero:before{
    content: '';
    position: absolute;
    top:0;
    bottom:0;
    left:0;
    right:0;
    background-color: rgba(0,0,0,.4);
}
#hero h1 {
    margin: 0;
    font-size: 48px;
    font-weight: 700;
    line-height: 56px;
    color: #fff;
    font-family: "Poppins", sans-serif;
}
#hero h2 {
    color: #eee;
    margin: 10px 0 0 0;
    font-size: 24px;
}
#hero .btn-get-started {
    font-family: "Raleway", sans-serif;
    font-weight: 500;
    font-size: 15px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 35px;
    border-radius: 50px;
    transition: 0.5s;
    margin-top: 30px;
    border: 2px solid #fff;
    color: #fff;
}
#hero .btn-get-started:hover {
    background-color:  #35cd3a;
    border: 2px solid  #35cd3a;
    color: #fff;
}
/* about */
.about ul{
    list-style: none;
    padding: 0;
}
.about ul i{
    color: #3ac162;
    margin-right: 0.5rem;
}
/* section */
section {
    padding: 60px 0;
    overflow: hidden;
}
.section-title {
    padding-bottom: 40px;
}
.section-title h2 {
    font-size: 14px;
    font-weight: 500;
    padding: 0;
    line-height: 1px;
    margin: 0 0 5px 0;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: #aaaaaa;
    font-family: "Poppins", sans-serif;
}
.section-title h2::after {
    content: "";
    width: 120px;
    height: 1px;
    display: inline-block;
    background: #9ae1af;
    margin: 4px 10px;
}
.section-title p {
    margin: 0;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    font-family: "Poppins", sans-serif;
    color: #37423b;
}
.learn-more-btn {
    background: #5fcf80;
    color: #fff;
    border-radius: 50px;
    padding: 8px 25px 9px 25px;
    white-space: nowrap;
    transition: 0.3s;
    font-size: 14px;
    display: inline-block;
}
.learn-more-btn:hover {
    background: #3ac162;
    color: #fff;
}
.content h3 {
    font-weight: 600;
    font-size: 26px;
}
.content ul li {
    padding-bottom: 10px;
}
.font-italic {
    font-style: italic!important;
}
.member {
    text-align: center;
    margin-bottom: 20px;
    background: #fff;
    border: 1px solid #eef0ef;
}
.member .member-content {
    padding: 0 20px 30px 20px;
}
.member img {
    margin: -1px -1px 30px -1px;
}
.member h4 {
    font-weight: 700;
    margin-bottom: 2px;
    font-size: 18px;
}
.member span {
    font-style: italic;
    display: block;
    font-size: 13px;
}
.member p {
    padding-top: 10px;
    font-size: 14px;
    font-style: italic;
    color: #aaaaaa;
}
